.modalBlock {
  display: flex;
  width: 100%;
  /* height: 100vh; */
  border-radius: 10px;
  top: 30px;
  left: -30px;
  position: absolute;
  justify-content: center;
  align-items: center;
  box-shadow: 0 10px 10px 0 #505050;

  .modalContent {
    display: flex;
    position: relative;
    direction: column;
    width: 680px;
    height: 75vh;
    z-index: 10;
    border-radius: 16px;
    .sliderSpan {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 100%;
      input {
        width: 60%;
      }
    }
    .modalHeader {
      display: flex;
      z-index: 20;
      // flex-flow: column-reverse;
      height: 50px;
      width: 100%;
      background: white;
      overflow: hidden;
      border-top-left-radius: 16px;
      border-top-right-radius: 16px;
      p {
        flex: 8;
        margin-left: 30px;
      }

      span {
        flex: 1;
        display: flex;
        justify-content: center;
        /* background: none; */
        width: 20%;
        align-self: center;
        justify-self: center;
        height: 20px;
        border: none;
        cursor: pointer;
      }
    }
    .modalBody {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 97%;
      height: 100%;
      position: absolute;
      border-radius: 20px;
      background-color: white;
      padding: 10px;
      font-size: 14px;
      color: blue;
      text-align: center;
      canvas {
      }
    }
  }

  .modalFooter {
    position: absolute;
    z-index: 20;
    bottom: 0;
    width: 100%;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
    height: 55px;
    background-color: rgb(228, 228, 228);
    display: flex;
    justify-content: center;
  }
}

.modalDisable {
  display: none;
}

.darkBG {
  background-color: rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 100vh;
  z-index: 0;
  // top: 50%;
  // left: 50%;
  // transform: translate(-50%, -50%);
  position: absolute;
}

.importDiv {
  display: flex;
  flex-direction: column;
  line-height: 60px;

  button {
    display: -ms-inline-flexbox;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-height: 62px;
    box-sizing: border-box;
    background: #492de5;
    padding: 0px 99px;
    border: none;
    font-weight: 600;
    font-size: 20px;
    color: #fff;
    transition: all 0.1s linear;
    border-radius: 8px;
    box-shadow: 0 3px 6px 0rgba 0, 0, 0, 0.14;
    order: 1;
    max-width: 60vw;
    cursor: pointer;
    &:hover {
      background: #664eec;
    }
  }
}
